
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {httpUrl} from '../../http_url';

export default {
name: 'HelloWorld',
data () {
return {
phone: '',
password: ''
}
},
components: {
Group,
Cell,
XInput,
XButton
},
methods:{
goQuickLogin(){
this.$router.push('/quick_login');
},
submitLogin(){
const phone = this.SpaceTransformation(this.phone);
let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
password:this.password
})
if(this.isEmpty(phone)){
this.$vux.toast.show({
type:'warn',
text: '请输入手机号'
})
return
}else if(!this.Val.isPhoneNum(phone)){
this.$vux.toast.show({
type:'warn',
text: '请输入正确手机号'
})
return
}else if(this.isEmpty(this.password)){
this.$vux.toast.show({
type:'warn',
text: '请输入登录密码'
})
return
}else {
this.$axios({
method: 'post',
url:httpUrl.passwordLogin,
data:postData,
headers: {
'Content-Type': 'application/x-www-form-urlencoded',
}
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/home');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}

},
forgetPassword(){
this.$router.push('/forget_psd');
}
}
}



